@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.headerContainer {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* Header Socials */

.headerSocials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem
}

.headerSocials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ME */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* SCROLL DOWN */
.scrollDown {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    header {
        height: 75vh;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
        padding-top: 3rem;
    }

    .headerSocials, .scrollDown{
        display: none;
    }
    .me {
        margin-top: 2rem;
    }

    h1 {
        line-height: 1;
    }
}

@media screen and (max-height: 600px) {
    header {
        height: 100vh;
        padding-top: 3rem;
    }

    .me {
        margin-top: 2rem;
    }
}

@media screen and (min-height: 601px) and (min-width: 601px) {
    header {
        height: 100vh;
    }
}
nav {
    background-color: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 1rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background-color: rgba(0, 0, 0, .3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}
.aboutContainer {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.aboutMe {
    width: 80%;
    margin-top: 1rem;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        transparent,
        var(--color-white),
        transparent
    );
    display: grid;
    place-items: center;
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
    transition: var(--transition);
}

.aboutMeImage {
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    border-radius: 5rem;
    overflow: hidden;
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    transition: var(--transition);
}

.aboutMeImage:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: var(--transition);
}

.aboutMe:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: var(--transition);
}

.aboutCards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3.5rem;
    gap: 3.5rem;
}

.aboutCard {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
}

.aboutCard:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.aboutIcon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.aboutCard h5 {
    font-size: 0.95rem;
}

.aboutCard small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.aboutContent p {
    margin: 2rem 0.2rem;
    text-align: justify;
    color: var(--color-light);
}

/* MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    .aboutContainer {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }

    .aboutMe {
        width: 37%;
        margin: 2rem auto 4rem;
    }

    .aboutContent {
        text-align: center;
    }

    .aboutContent p {
        margin: 1rem 0 1.5rem;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .aboutMe {
        width: 50%;
        margin: 0 auto 4rem;
    }

    .aboutCards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .aboutContent {
        text-align: center;
    }

    .aboutContent p {
        margin: 1.5rem 0;
    }
}
.experienceContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experienceContainer > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}

.experienceContainer > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experienceContainer > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experienceContent {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experienceDetails {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experienceDetailsIcon {
    color: var(--color-primary);
    margin-top: 6px;
}

/* MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    .experienceContainer {
        grid-template-columns: 1fr;
    }

    .experienceContainer > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experienceContent {
        padding: 1rem;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .experienceContainer {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experienceContainer > div {
        width: 100%;
        padding: 2rem 1rem;
    }
}
.portofolioContainer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
}

.portofolioItem {
    background: var(--color-bg-variant);
    padding: 0.5rem;
    border-radius: 1.5rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portofolioItem:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portofolioItemImage {
    border-radius: 1rem;
    overflow: hidden;
}

.portofolioItemImage img {
    height: 150px;
}

.portofolioItem h3 {
    margin-top: 1rem;
    height: 75px;
}

.portofolioItemCta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

@media screen and (max-width: 1024px) {
    .portofolioContainer {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .portofolioItem {
        padding: 0.8rem;
    }
    .portofolioItemImage img {
        height: 200px;
    }
    .portofolioItem h3 {
        height: 60px;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .portofolioContainer {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1.8rem;
        gap: 1.8rem;
    }
    .portofolioItem {
        padding: 1rem;
    }
}
.container.contactContainer {
    width: 60%;
    display: grid;
    grid-template-columns: 30% 55%;
    grid-gap: 15%;
    gap: 15%;
}

.contactOptions {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contactOption {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
    overflow-wrap: break-word;
}

.contactOption:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contactOptionIcon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    color: var(--color-primary);
}

.contactOption a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* Form */

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}

/* MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    .container.contactContainer {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .container.contactContainer {
        width: var(--container-width-sm);
    }
}
.footerLogo {
    font-family: 'Shadows Into Light', cursive;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    display: inline-block;
}

footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footerSocials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footerSocials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footerSocials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footerCopyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* SMALL DEVICE */

@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }

    .footerSocials {
        margin-bottom: 2.5rem;
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1a1a1c;
    --color-bg-variant: #c5bdafbb;
    --color-primary: #ac361f;
    --color-primary-variant: #7f6a41;
    --color-white: #fff;
    --color-light: rgba(255,255,255, 0.6);
    /* #c5bdafbb */
    /* #872e1e */
    /* #7f6a41 */
    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Prompt', sans-serif;
    background: #1a1a1c;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}

/* GENERAL STYLES */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(255,255,255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #ac361f;
    color: var(--color-primary);
    margin-bottom: 3rem;
} 

.text-light {
    color: rgba(255,255,255, 0.6);
    color: var(--color-light);
}

a {
    color: #ac361f;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #ac361f;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #ac361f;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #1a1a1c;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #ac361f;
    background: var(--color-primary);
    color: #1a1a1c;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 6rem;
    }
}
