.portofolioContainer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
}

.portofolioItem {
    background: var(--color-bg-variant);
    padding: 0.5rem;
    border-radius: 1.5rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portofolioItem:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portofolioItemImage {
    border-radius: 1rem;
    overflow: hidden;
}

.portofolioItemImage img {
    height: 150px;
}

.portofolioItem h3 {
    margin-top: 1rem;
    height: 75px;
}

.portofolioItemCta {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

@media screen and (max-width: 1024px) {
    .portofolioContainer {
        grid-template-columns: repeat(2, 1fr);
        gap: 1.5rem;
    }
    .portofolioItem {
        padding: 0.8rem;
    }
    .portofolioItemImage img {
        height: 200px;
    }
    .portofolioItem h3 {
        height: 60px;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .portofolioContainer {
        grid-template-columns: repeat(1, 1fr);
        gap: 1.8rem;
    }
    .portofolioItem {
        padding: 1rem;
    }
}