.footerLogo {
    font-family: 'Shadows Into Light', cursive;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    display: inline-block;
}

footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footerSocials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footerSocials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footerSocials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footerCopyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

/* SMALL DEVICE */

@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.2rem;
    }

    .footerSocials {
        margin-bottom: 2.5rem;
    }
}