.aboutContainer {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.aboutMe {
    width: 80%;
    margin-top: 1rem;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        transparent,
        var(--color-white),
        transparent
    );
    display: grid;
    place-items: center;
    transform: rotate(-10deg);
    transition: var(--transition);
}

.aboutMeImage {
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    border-radius: 5rem;
    overflow: hidden;
    transform: rotate(20deg);
    transition: var(--transition);
}

.aboutMeImage:hover {
    transform: rotate(0);
    transition: var(--transition);
}

.aboutMe:hover {
    transform: rotate(0);
    transition: var(--transition);
}

.aboutCards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.5rem;
}

.aboutCard {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
}

.aboutCard:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.aboutIcon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.aboutCard h5 {
    font-size: 0.95rem;
}

.aboutCard small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.aboutContent p {
    margin: 2rem 0.2rem;
    text-align: justify;
    color: var(--color-light);
}

/* MEDIUM DEVICES */

@media screen and (max-width: 1024px) {
    .aboutContainer {
        grid-template-columns: 1fr;
        gap: 2rem;
    }

    .aboutMe {
        width: 37%;
        margin: 2rem auto 4rem;
    }

    .aboutContent {
        text-align: center;
    }

    .aboutContent p {
        margin: 1rem 0 1.5rem;
    }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px) {
    .aboutMe {
        width: 50%;
        margin: 0 auto 4rem;
    }

    .aboutCards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .aboutContent {
        text-align: center;
    }

    .aboutContent p {
        margin: 1.5rem 0;
    }
}